@font-face {
  font-family: "PTMono";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local(PTMono-Regular), url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local(RobotoCondensed-Regular), url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local(RobotoCondensed-Bold), url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local(Roboto-Medium), url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff"); }

.body {
  margin: 0;
  position: relative;
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
  font-family: "PTMono", Arial, sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: black;
  background-color: white; }

.container {
  margin: 0 auto;
  max-width: 1440px; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  overflow: hidden; }

.link {
  text-decoration: none; }

.list {
  list-style: none;
  margin: 0;
  padding: 0; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

.header {
  position: relative;
  color: white;
  background-color: #1b2d37;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-image: url("../img/background/header-background-waves@1x.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  overflow-x: hidden; }
  @media (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .header {
      background-image: url("../img/background/header-background-waves@2x.png"); } }

.header__wrapper {
  padding: 33px 15px 100px 15px; }

.header__nav-grid-layout {
  display: grid;
  grid-template-columns: 1fr auto; }

.header__logo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 205px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.header__logo > span {
  margin-left: 5px;
  display: block;
  font-family: "Roboto Condensed", Arial, sans-serif;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px; }

.header__logo span > span {
  display: block;
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #788891; }

.header__nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.header__nav-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly; }

.header__nav-link {
  margin: 10px 40px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  line-height: 18px;
  color: white; }

.header__nav-phone {
  white-space: nowrap; }

.header__nav-link svg {
  margin-right: 15px;
  color: #00b2ff; }

.header__nav-button {
  padding: 12px 0;
  width: 180px;
  text-align: center;
  border: 1px solid white;
  border-radius: 5px;
  cursor: pointer; }

.company-info__grid-layout {
  display: grid;
  grid-template-columns: 720px 1fr; }

.company-info__wrapper {
  padding-top: 231px;
  position: relative;
  min-height: 780px; }
  .company-info__wrapper::before {
    content: "";
    position: absolute;
    top: calc(50% - 250px);
    left: 800px;
    display: block;
    background-image: url("../img/background/header-background-board@1x.png"), url("../img/background/background-two-triangles.svg");
    background-repeat: no-repeat;
    background-size: contain, 637px;
    background-position: center, 0 -130px;
    width: 705px;
    height: 515px;
    overflow: hidden; }
  @media (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .company-info__wrapper::before {
      background-image: url("../img/background/header-background-board@2x.png"), url("../img/background/background-two-triangles.svg"); } }

.company-info h2 {
  margin: 0 0 31px 0;
  position: relative;
  font-family: "Roboto Condensed", Arial, sans-serif;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase; }

.company-info p {
  margin: 0 0 37px 0;
  padding-left: 98px;
  position: relative;
  font-family: "PTMono", Arial, sans-serif;
  font-size: 22px;
  line-height: 34px; }
  .company-info p::before {
    content: "";
    position: absolute;
    left: 0;
    top: 15px;
    display: block;
    width: 66px;
    height: 2px;
    background-color: white; }

.company-info__button {
  margin-left: 98px;
  padding: 15px 0;
  display: block;
  width: 407px;
  font-family: "Roboto Condensed", Arial, sans-serif;
  font-weight: 700;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: #00b2ff;
  border-radius: 5px;
  cursor: pointer; }
